@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,500,300,700);
.navbar {
  background-color: #292622;
  height: 60px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.navbar-fixed {
  position: fixed;
  width: 100%;
}

.full-width {
  width: 100%;
}

.margin-left-30 {
  margin-left: 30px;
}

.margin-right-30 {
  margin-right: 30px;
}

.menu-bars {
  margin-left: 2rem;
  font-size: 1.5rem;
  background: none;
  cursor: pointer;
  padding: 5px 10px;
  border-radius: 5px;
}

.menu-bars:hover {
  background-color: #d16318;
}

.footer-title-icon {
  font-size: 30px;
  margin-left: 5px;
}

.brand-title {
  font-size: 35px;
  cursor: pointer;
}

.brand-title-subpart {
  color: lightseagreen;
}

.navbar-items-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;  
}

.navbar-buttons {
  display: flex;
  align-items: center;
}

.navbar-btn {
  font-weight: 600;
  padding: 5px 10px;
  border-radius: 5px;
  cursor: pointer;
}

.sign-up-button {
  background-color: #DC3545;
  color: white;
}

.sign-in-button {
  background-color: #0d6efd;
  color: white;
}

.sign-in-button-icon {
  margin-left: 5px;  
}

.tree-main-menu {
  color: #f5f5f5;
  font-size: 18px;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 4px;
  cursor: pointer;
}

.tree-main-menu-text {
  opacity: 0;
  transition: 0.2s;
  color: #f5f5f5;
}

.tree-main-menu-icon {
  margin-right: 15px;
}

.tree-main-menu:hover .tree-main-menu-text {
  opacity: 1;
}

.tree-main-menu:hover {
  background-color: #d16318;
  width: 100%;
}

.margin-left-5 {
  margin-left: 5px;
}

/* ---- CSS for folder specifications ------ */
.tree-new-folder {
  position: relative;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 3px 0px 3px 0px;
  list-style: none;
  transition: 0.2s;
}

.tree-folder-container {
  position: relative;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 3px 0px 3px 0px;
  list-style: none;
  transition: 0.2s;
}

.tree-folder-specifications {
  color: #f5f5f5;
  font-size: 17px;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 14px;
  border-radius: 4px;
}

.folder-text {
  white-space: nowrap;
  width: 200px;
  overflow: hidden;
  text-overflow: ellipsis;
}

.tree-folder-container:hover {
  background-color: #d16318;
  border-radius: 5px;
}

.tree-folder-icon-specification {
  position: absolute;
  background-color: #d16318;
  height: 25px;
  right: 3%;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  transition: 0.3s;
}

.tree-folder-icon {
  padding-left: 5px;
  padding-right: 5px;
  cursor: pointer;
}

.tree-folder-container:hover .tree-folder-icon-specification {
  opacity: 1;
}

.create-field {
  border-color: #d16318;
}

.create-field:focus-visible {
  border-color: #d16318;
  box-shadow: 0 0 0 0.1rem #d16318;
  outline: 0;
  border-radius: 2px;
}

/* ---- CSS for file specifications ------ */
.tree-file-container {
  position: relative;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 3px 0px 3px 0px;
  list-style: none;
  transition: 0.2s;
}

.tree-file-specifications {
  color: #f5f5f5;
  font-size: 17px;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 14px;
  border-radius: 4px;
}

.file-text {
  white-space: nowrap;
  width: 200px;
  overflow: hidden;
  text-overflow: ellipsis;
}

.tree-file-container:hover {
  background-color: #d16318;
  border-radius: 5px;
}

.tree-file-icon-specification {
  position: absolute;
  background-color: #d16318;
  height: 25px;
  right: 3%;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  transition: 0.2s;
}

.tree-file-icon {
  padding-left: 10px;
  padding-right: 10px;
  cursor: pointer;
}

.tree-file-container:hover .tree-file-icon-specification {
  opacity: 1;
}

/* ------------------- Common CSS ---------------------- */
.margin-left-10 {
  margin-left: 10px;
}

.margin-left-22 {
  margin-left: 22px;
}

.nav-menu {
  background-color: #1d1d1d;
  width: 250px;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  left: -100%;
  transition: 850ms;
  z-index: 999;
}

.nav-menu.active {
  left: 0;
  transition: 350ms;
}

.nav-text {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 8px 0px 8px 0px;
  list-style: none;
  height: 60px;
}

.sidebar-main-menu {
  color: #f5f5f5;
  font-size: 18px;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 4px;
  cursor: pointer;
}

.sidebar-main-menu:hover {
  background-color: #d16318;
}

.nav-text-item {
  color: #f5f5f5;
  font-size: 18px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 4px;
  cursor: pointer;
}

.nav-text-item:hover {
  background-color: #d16318;
}

.nav-menu-items {
  width: 100%;
}

.navbar-toggle {
  background-color: #f5f5f5;
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.folder-icon {
  margin-right: 16px;
  cursor: pointer;
}

.cell-list {
  margin: 0 25px 50vh;
}

.react-draggable-transparent-selection .cell-list {
  margin-bottom: 100vh;
}

.cell-list-item {
  position: relative;
}

.action-bar-wrapper {
  height: 30px;
  width: 100%;
  background-color: #403831;
}

.progress-wrapper {
  height: 100%;
  flex-grow: 1;
  background-color: white;
}

.progress-cover {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  padding-left: 10%;
  padding-right: 10%;
  -webkit-animation: fadeIn 0.5s;
          animation: fadeIn 0.5s;
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

