}

.add-cell {
  position: relative;
  display: flex;
  justify-content: center;
  opacity: 0;
  transition: opacity 0.3s ease-in 0.1s;
  margin: 10px 0;
}

.add-cell:hover {
  opacity: 1;
}

.add-cell.force-visible {
  opacity: 1;
}

.add-cell .divider {
  position: absolute;
  top: 50%;
  bottom: 50%;
  border-bottom: 1px solid gray;
  width: 100%;
  z-index: -1;
}

.add-cell button {
  margin: 0 40px;
}

