  -webkit-animation: move 7.1s infinite;
          animation: move 7.1s infinite;
}

@-webkit-keyframes move {
  0% {
    -webkit-transform: rotate(40deg);
            transform: rotate(40deg);
  }

  50% {
    -webkit-transform: rotate(-40deg);
            transform: rotate(-40deg);
  }

  100% {
    -webkit-transform: rotate(40deg);
            transform: rotate(40deg);
  }
}

@keyframes move {
  0% {
